<template>
  <div class="role-manage">
    <div class="button-group">
      <span class="route-name">{{$route.meta.title}}</span>
      <el-button type="warning" size="small" icon="el-icon-plus" @click="handleAdd">添加</el-button>
    </div>
    <el-table :data="roleDataList" stripe v-loading="tableLoading" style="width: 100%">
      <el-table-column prop="role_id" label="序号" width="70">
      </el-table-column>
      <el-table-column prop="role_name" label="角色名">
      </el-table-column>
      <el-table-column prop="remark" label="备注">
      </el-table-column>
      <el-table-column label="操作" align="center" width="200">
        <template slot-scope="scope">
          <el-button size="mini" type="text" icon="el-icon-edit" @click="handleEdit(scope.row)">编辑</el-button>
          <el-button size="mini" type="text" icon="el-icon-delete" @click="handleDelete(scope.row)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination background @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage" :page-size="10" layout="total, prev, pager, next, jumper" :total="totalCount">
    </el-pagination>
    <!-- 操作弹框 -->
    <el-dialog :title="handleTitle" :visible.sync="handleVisible" width="30%" center>
      <el-form ref="roleForm" :model="roleForm" status-icon label-width="90px" size="small">
        <el-form-item label="角色名">
          <el-row>
            <el-col :span="24">
              <el-input v-model.trim="roleForm.role_name" maxlength="50" type="text"></el-input>
            </el-col>
          </el-row>
        </el-form-item>
        <el-form-item label="备注">
          <el-row>
            <el-col :span="24">
              <el-input v-model.trim="roleForm.remark" maxlength="100" type="text"></el-input>
            </el-col>
          </el-row>
        </el-form-item>
        <el-form-item label="菜单权限">
          <el-row>
            <el-col :span="24">
              <div class="tree-container" v-if="handleVisible" key="roleTreeForm">
                <el-tree ref="menuTree"
                  show-checkbox class="tree-body"
                  :default-expand-all="true"
                  :data="roleMenuTree"
                  node-key="id"
                  :default-checked-keys="roleTreeData"
                  :props="defaultProps">
                </el-tree>
              </div>
            </el-col>
          </el-row>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button size="small" type="primary" @click="handleConfirm">确 定</el-button>
        <el-button size="small" @click="handleVisible = false">取 消</el-button>
      </span>
    </el-dialog>
    <!-- 删除弹窗 -->
    <el-dialog title="删除角色" :visible.sync="delVisible" width="260px" center>
      <div class="del-dialog-cnt">删除不可恢复，是否确定删除？</div>
      <span slot="footer" class="dialog-footer">
        <el-button size="small" type="danger" @click="deleteRow">确 定</el-button>
        <el-button size="small" @click="delVisible = false">取 消</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex'
import Api from '../../constants/Api'
export default {
  data() {
    return {
      handleTitle: '',
      handleVisible: false,
      delVisible: false,
      tableLoading: true,
      currentPage: 1,
      totalCount: 10,
      pageSize: 10,
      defaultProps: {
        children: 'children',
        label: 'text'
      },
      roleTreeData: [], // 树桩选点数据
      roleForm: {
        role_name: '',
        remark: '',
        menu_ids: [] // 菜单对应的id列表
      }
    }
  },
  computed: {
    ...mapState({
      roleMenuTree: state => state.authority.roleMenuTree,
      roleDataList: state => state.authority.roleDataList
    })
  },
  mounted() {
    const vm = this
    vm.getData()
    vm.getRoleMenuTree(vm)
  },
  methods: {
    ...mapActions('authority', ['getRoleList', 'getRoleMenuTree', 'addRole', 'editRole', 'removeRole']),
    resetForm() {
      this.roleForm = {
        role_name: '',
        remark: '',
        menu_ids: []
      }
    },
    getData() {
      const vm = this
      vm.getRoleList(vm)
    },
    handleSizeChange(val) {
      this.pageSize = val
      this.getData()
    },
    handleCurrentChange(val) {
      this.currentPage = val
      this.getData()
    },
    handleAdd() {
      this.resetForm()
      this.handleTitle = '添加角色'
      this.handleVisible = true
    },
    handleEdit(row) {
      const vm = this
      vm.roleForm = {
        role_id: row.role_id,
        role_name: row.role_name,
        remark: row.remark,
        menu_ids: row.menu_ids
      }
      vm.handleTitle = '修改角色'
      let url = Api.getRoleMenu.replace('{roleId}', row.role_id)
      vm.$axios({
        method: 'get',
        url: url
      }).then(res => {
        let box = []
        res.data.forEach((value) => {
          if (value !== -1) {
            let ck = value + ''
            box.push(ck)
          }
        })
        this.roleTreeData = box
        vm.handleVisible = true
      })
    },
    handleDelete(row) {
      this.roleForm.role_id = row.role_id
      this.delVisible = true
    },
    handleConfirm() {
      const vm = this
      if (vm.roleForm.role_name === '') {
        vm.$message({
          type: 'warning',
          message: '角色名不为空'
        })
        return
      }
      vm.roleForm.menu_ids = this.$refs.menuTree.getCheckedKeys().concat(this.$refs.menuTree.getHalfCheckedKeys()).map(Number)
      if (vm.handleTitle === '添加角色') {
        vm.addRole(vm)
      }
      if (vm.handleTitle === '修改角色') {
        vm.editRole(vm)
      }
      vm.handleVisible = false
    },
    deleteRow() {
      this.removeRole(this)
      this.delVisible = false
    }
  }
}
</script>

<style type="text/scss" lang="scss" scoped>
.role-manage {
  .tree-container {
    height: 240px;
    width: 100%;
    overflow-y: auto;
  }
}
</style>



